<template>
  <p @click="add">
    {{ name }}
  <br>
      
  </p>count: {{ cnt }}
  <br>
  <input type="text" v-model="name"/>
</template>

<script>
export default {
  name: 'HelloWorld',
  data(){
    return{
      cnt: 0,
      name: 'click me',
    }
  },
  methods:{
    add(){
      ++this.cnt;
    }
  }
}
</script>

